<script>
export default {
  name: 'DoctorList',
  data() {
    return {
      department: '',
    }
  },
  created() {
    // 
    this.department = this.$route.query.department || '';
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    goToAppointment(doctorName, doctorTitle, rating, date) {
      // /
      let hasAppointment = doctorName !== ''; // 
      let doctorImage = '';
      
      // 
      if (doctorName === '') {
        doctorImage = 'doctor1.png';
      } else if (doctorName === '') {
        doctorImage = 'doctor2.png';
      } else if (doctorName === '') {
        doctorImage = 'doctor3.png';
      } else if (doctorName === '') {
        doctorImage = 'doctor4.png';
      }
      
      // 
      let fee = 10; // 
      if (doctorTitle === '') {
        fee = 20;
      } else if (doctorTitle === '') {
        fee = 15;
      } else if (doctorTitle === '') {
        fee = 10;
      }
      
      // 
      this.$router.push({
        path: '/appointment-payment',
        query: {
          doctorName: doctorName,
          doctorTitle: doctorTitle,
          rating: rating,
          date: date,
          hasAppointment: hasAppointment,
          doctorImage: doctorImage,
          fee: fee
        }
      });
    }
  }
}
</script>

