<script>
export default {
  name: 'MedicalReminder',
  data() {
    return {
      isPaid: false,
      fromPaymentSuccess: false,
      completedExam: '' // 
    }
  },
  created() {
    // 
    this.fromPaymentSuccess = this.$route.query.fromPaymentSuccess === 'true';
    
    // 
    this.completedExam = this.$route.query.completedExam || localStorage.getItem('completedExam') || '';
    
    // PaymentSuccesscompletedExam
    if (this.$route.query.fromPaymentSuccess === 'true' && !this.$route.query.completedExam) {
      localStorage.removeItem('completedExam');
      this.completedExam = '';
    }
    
    // 
    if (this.$route.query.fromNavigation === 'true') {
      // 
      localStorage.removeItem('paymentStatus');
      this.isPaid = false;
    } else {
      // 
      this.checkPaymentStatus();
    }
  },
  methods: {
    // 
    getProgressImage() {
      // 
      if (this.completedExam) {
        //  load13.png
        return 'load13.png';
      } else {
        // 
        return this.fromPaymentSuccess ? 'load8.png' : 'load6.png';
      }
    },
    
    // 
    goToHomePage() {
      // 
      //  allExamsCompleted 
      localStorage.removeItem('allExamsCompleted');
      this.$router.push('/home');
    },
    
    // ...
    // 
    goToDialectTranslation() {
      this.$router.push('/dialect-translation');
    },
    // 
    goToPayment() {
      if (!this.isPaid) {
        this.$router.push('/payment');
      }
    },
    // 
    checkPaymentStatus() {
      // 
      const paymentStatus = localStorage.getItem('paymentStatus');
      this.isPaid = paymentStatus === 'completed';
    },
    // 
    goToNavigationpage() {
      // 
      if (this.isPaid) {
        this.$router.push({
          path: '/navigation-page',
          query: { 
            fromMedicalReminder: 'true'
          }
        });
      }
    },
    // 
    startQueue(examType) {
      // 
      localStorage.setItem('currentExamType', examType);
      
      // 
      const completedExam = localStorage.getItem('completedExam');
      
      // 
      this.$router.push({
        path: '/queue-progress',
        query: { 
          examType: examType,
          status: 'waiting',
          completedExam: completedExam || ''
        }
      });
    },
    // 
    //  checkQueueProgress 
    checkQueueProgress() {
    // 
    const completedExam = this.completedExam;
    
    // 
    if (completedExam === '') {
    // 
    this.startQueue('');
    } else if (completedExam === '') {
    // 
    this.startQueue('');
    } else {
    // 
    // 36
    this.startQueue('');
    }
    }
  }
}
</script>
 


