<script>
export default {
  name: 'HomePage',
  data() {
    return {
      allExamsCompleted: false,
      showReportView: false
    }
  },
  created() {
    // 
    const allExamsCompleted = localStorage.getItem('allExamsCompleted') === 'true';
    
    // 
    const showReportPage = this.$route.query.showReportPage;
    
    // 
    if (allExamsCompleted) {
      this.allExamsCompleted = true;
      // 
      this.showReportView = true;
    }
    
    // 
    if (showReportPage === 'false') {
      this.allExamsCompleted = false;
      this.showReportView = false;
    } else if (showReportPage === 'true') {
      // 
      this.allExamsCompleted = true;
      this.showReportView = true;
    }
    
    console.log(':', {
      allExamsCompleted: this.allExamsCompleted,
      showReportView: this.showReportView,
      routeQuery: this.$route.query
    });
  },
  methods: {
    // 
    goToRegisterNotice() {
      this.$router.push('/register-notice');
    },
    goToReportPage() {
      // 
      this.$router.push('/report-page');
    },
    goToFollowUpPage() {
      // 
      this.$router.push({
        path: '/navigation-page',
        query: { 
          fromReport: 'true'
        }
      });
    },
    goToNavigationPage() {
      // 
      this.$router.push({
        path: '/navigation-page',
        query: { 
          fromReport: 'false',
          fromHomePage: 'true'
        }
      });
    },
    goToAskPage() {
      // 
      this.$router.push('/ask-page');
    },
    
    goToGuideProcess() {
      // 
      this.$router.push({
        path: '/guide-process',
        query: { 
          fromReportPage: this.allExamsCompleted ? 'true' : 'false'
        }
      });
    },
    
    goToMyPage() {
      // 
      this.$router.push({
        path: '/my-page',
        query: { 
          fromReportView: this.showReportView ? 'true' : 'false'
        }
      });
      console.log(':', this.showReportView ? '' : '');
    }
  }
}
</script>

